import { Injectable } from '@angular/core';
import { HttpClient } from "@angular/common/http";

import { User } from "../models/user";
import "rxjs/add/operator/map";

@Injectable()
export class LoginService {
    domain: string = 'http://localhost:3000';
    // domain: string = 'www.mydomainapi.com/';

    constructor(private http: HttpClient) { }

    getLogins() {
        return this.http.get<User[]>(`${this.domain}/api/login`).map(res => res);
    }

    addLogin(newUser: User) {
        return this.http.post<User>(`${this.domain}/api/login`, newUser).map(res => res);
    }

    deleteLogin(id) {
        return this.http.delete<User>(`${this.domain}/api/login/${id}`).map(res => res);
    }

    updateLogin(newUser) {
        return this.http.put<User>(`${this.domain}/api/login/${newUser._id}`, newUser);
    }
}



